
.body-container {
	margin: 50px;
	text-align: center;
}

.scatter-plot {
	display: inline-block;
	width: 80%;
	height: 500px;
	margin: 20px;
}

.welcome {
	margin-top: 20%;
}

.ui.segment:first-child {
	text-align: center;
}

.drop-down {
	margin-top: 20px;
}

/* Tabs */
.tab-list {
  border-bottom: 1px solid #ccc;
  padding-left: 0;
}

.tab-list-item {
  display: inline-block;
  list-style: none;
  margin-bottom: -1px;
  padding: 0.5rem 0.75rem;
}

.tab-list-active {
  background-color: white;
  border: solid #ccc;
  border-width: 1px 1px 0 1px;
}

.ui.grid>.column:not(.row), .ui.grid>.row>.column {
	padding-left: 0 !important;
	padding-right: 0 !important;
}


